<h1>Switch 消息提示</h1>
<hr>
<h3>基础用法</h3>
<p>表示两种相互对立的状态间的切换，多用于触发「开/关」。
绑定v-model到一个Boolean类型的变量。可以使用active-color属性与inactive-color属性来设置开关的背景色。</p>
<div class="demo-block">
  <t-switch v-model="switchValue1" active-color="#00a53c" inactive-color="#ccc"></t-switch>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-switch v-model="switchValue1" active-color="#00a53c" inactive-color="#ccc"></t-switch>
</div>




</body></html></div>
