  <span class="hljs-tag">&lt;<span class="hljs-name">t-switch</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"switchValue1"</span> <span class="hljs-attr">active-color</span>=<span class="hljs-string">"#00a53c"</span> <span class="hljs-attr">inactive-color</span>=<span class="hljs-string">"#ccc"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">t-switch</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>


<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
