    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">switchValue1</span>: <span class="hljs-literal">true</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>文字描述</h3>
<p>使用show-text控制是否显示文字描述,使用active-text属性与inactive-text属性来设置开关的文字描述。</p>
<div class="demo-block">
  <t-switch v-model="switchValue2" active-color="#13ce66" inactive-color="#ff4949" :show-text="true" active-text="开" inactive-text="关"></t-switch>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-switch v-model="switchValue2" active-color="#13ce66" inactive-color="#ff4949" :show-text="true" active-text="开" inactive-text="关"></t-switch>
</div>




</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
