</div></demo-block>
<h3>禁用状态</h3>
<p>设置disabled属性，接受一个Boolean，设置true即可禁用。</p>
<div class="demo-block">
  <t-switch v-model="switchValue3" disabled></t-switch>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
  <t-switch v-model="switchValue3" disabled></t-switch>
