<h3>单独引用</h3>
<pre v-pre=""><code v-pre="" class="language-javascript"><span class="hljs-keyword">import</span> { TSwitch } <span class="hljs-keyword">from</span> <span class="hljs-string">'tty-ui'</span>;

</code></pre>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>width</td>
<td>switch 的宽度（像素）</td>
<td>number</td>
<td>—</td>
<td>40</td>
</tr>
<tr>
<td>active-icon-class</td>
<td>switch 打开时所显示图标的类名，设置此项会忽略 <code v-pre="" class="code_inline">active-text</code></td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>inactive-icon-class</td>
<td>switch 关闭时所显示图标的类名，设置此项会忽略 <code v-pre="" class="code_inline">inactive-text</code></td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>active-text</td>
<td>switch 打开时的文字描述</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>inactive-text</td>
<td>switch 关闭时的文字描述</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>active-value</td>
<td>switch 打开时的值</td>
<td>boolean / string / number</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>inactive-value</td>
<td>switch 关闭时的值</td>
<td>boolean / string / number</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>active-color</td>
<td>switch 打开时的背景色</td>
<td>string</td>
<td>—</td>
<td>#409EFF</td>
</tr>
<tr>
<td>inactive-color</td>
<td>switch 关闭时的背景色</td>
<td>string</td>
<td>—</td>
<td>#C0CCDA</td>
</tr>
<tr>
<td>name</td>
<td>switch 对应的 name 属性</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>switch 状态发生变化时的回调函数</td>
<td>新状态的值</td>
</tr>
</tbody>
</table>
<h3>Methods</h3>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>使 Switch 获取焦点</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

