<h1>Table 表格</h1>
<hr>
<h3>基础用法</h3>
<p>最基础形态小表格,配置<code v-pre="" class="code_inline">tableColumns</code>和<code v-pre="" class="code_inline">data</code>就可了，配置<code v-pre="" class="code_inline">td-brand</code>可单独单击<code v-pre="" class="code_inline">brand</code>那一列有事件</p>
<div class="demo-block">
  <t-table :data="tableData" :columns="tableColumns" :show-handle="true" :tdHeight="40" :tableId="demo1Id" @td-brand="clickStatus" @click-item="tableSelectItem">
    <template slot="operations" scope="scope">
      <span @click="edit(scope.item)">编辑</span>
      <span @click="delete(scope.item)">删除</span>
      <span @click="disable(scope.item)">禁用</span>
    </template>
  </t-table>
</div>
<demo-block>
