<h3>左边选择固定</h3>
<p>需要配置显示显示出来左右<code v-pre="" class="code_inline">:show-select=&quot;true&quot;</code>再<code v-pre="" class="code_inline">:select-fixed=&quot;true&quot;</code>(选择固定)，<code v-pre="" class="code_inline">pagination</code>分页传入</p>
<div class="demo-block">
  <t-table :data="tableData2" :columns="tableColumns2" :show-select="true" :show-handle="true" :select-fixed="true" :handle-fixed="false" :pagination="pagination" @click-item="tableSelectItem" @change-page="changePage">
    <template slot="operations" scope="scope">
      <span @click="edit(scope.item)">编辑</span>
      <span @click="edit(scope.item)">删除</span>
      <span @click="edit(scope.item)">禁用</span>
    </template>
  </t-table>
</div>
