<h3>左右固定 上下固定</h3>
<p>左右固定 上下固定需要指定高度<code v-pre="" class="code_inline">:scroll-height=&quot;300&quot;</code> 和 <code v-pre="" class="code_inline">:title-fixed=&quot;'fixed'</code>，其他的跟上面例子一样</p>
<div class="demo-block">
  <t-table :data="tableData2" :columns="tableColumns2" :show-select="true" :show-handle="true" :title-fixed="'fixed'" :scroll-height="300" :select-fixed="true" :handle-fixed="true" :pagination="pagination" @click-item="tableSelectItem" @change-page="changePage">
    <template slot="operations" scope="scope">
      <span @click="edit(scope.item)">编辑</span>
      <span @click="edit(scope.item)">删除</span>
      <span @click="edit(scope.item)">禁用</span>
    </template>
  </t-table>
</div>
