</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>单独引入</h3>
<pre v-pre=""><code v-pre="" class="language-javascript"><span class="hljs-keyword">import</span> { TTable, TTreeTable } <span class="hljs-keyword">from</span> <span class="hljs-string">'tty-ui'</span>;

</code></pre>
<h3>Options or Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>message</td>
<td>消息文字</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success/warning/info/danger</td>
<td>info</td>
</tr>
<tr>
<td>duration</td>
<td>显示时间, 毫秒。设为 0 则不会自动关闭</td>
<td>number</td>
<td>—</td>
<td>3000</td>
</tr>
<tr>
<td>closable</td>
<td>是否显示关闭按钮</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Methods</h3>
<p>调用 <code v-pre="" class="code_inline">Alert</code> 或 <code v-pre="" class="code_inline">this.$alert</code> 会返回当前 Alert 的实例。如果需要手动关闭实例，可以调用它的 <code v-pre="" class="code_inline">close</code> 方法。</p>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>关闭当前的 Message</td>
</tr>
</tbody>
</table>
<h4>列的配置（columns）</h4>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>默认</th>
<th>是否必须</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>-</td>
<td>是</td>
<td>表头标题</td>
</tr>
<tr>
<td>key</td>
<td></td>
<td>是</td>
<td>数据的 key 值</td>
</tr>
<tr>
<td>width</td>
<td>-</td>
<td>是</td>
<td>列的宽度以及最小宽度</td>
</tr>
<tr>
<td>textLine</td>
<td>2</td>
<td>否</td>
<td>指定那一列的单元格文子溢出多少行显示省略号</td>
</tr>
<tr>
<td>textAlign</td>
<td>left</td>
<td>否</td>
<td>指定那一列的单元格文字的对齐方式left（左对齐） center（ 居中） right(右对齐)</td>
</tr>
<tr>
<td>image</td>
<td>-</td>
<td>否</td>
<td>必须是图片的 url 链接</td>
</tr>
</tbody>
</table>
<h3>表格组件的属性配置说明（table config）</h3>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>默认</th>
<th>是否必须</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>tdata</td>
<td>Array</td>
<td>-</td>
<td>是</td>
<td>渲染表格的数据</td>
</tr>
<tr>
<td>tcolumns</td>
<td>Array</td>
<td>-</td>
<td>是</td>
<td>表格列的配置</td>
</tr>
<tr>
<td>showSelect</td>
<td>Boolean</td>
<td>false</td>
<td>否</td>
<td>是否显示左侧选择框</td>
</tr>
<tr>
<td>showHandle</td>
<td>Boolean</td>
<td>false</td>
<td>否</td>
<td>是否显示右侧操作内容</td>
</tr>
<tr>
<td>titleHeight</td>
<td>Number</td>
<td>32</td>
<td>否</td>
<td>表头高度</td>
</tr>
<tr>
<td>tdHeight</td>
<td>Number</td>
<td>50</td>
<td>否</td>
<td>单元格高度</td>
</tr>
<tr>
<td>titleFixed</td>
<td>String</td>
<td>'auto'</td>
<td>否</td>
<td>表头是否固定，默认'auto'（不固定），'fixed'(固定),注意：表头固定需指定滚动内容（scrollHight）的高度</td>
</tr>
<tr>
<td>scrollHight</td>
<td>Numer</td>
<td>400</td>
<td>否</td>
<td>滚动内容的高度</td>
</tr>
<tr>
<td>selectFixed</td>
<td>Boolean</td>
<td>false</td>
<td>否</td>
<td>左侧是否固定</td>
</tr>
<tr>
<td>handleFixed</td>
<td>Boolean</td>
<td>false</td>
<td>否</td>
<td>右侧是否固定</td>
</tr>
<tr>
<td>page</td>
<td>Object</td>
<td>-</td>
<td>否</td>
<td>不配置这个 page 就不显示 底部页码，配置的话{totalPage: 50,maxSize: 5} （totalPage)总页数 ,（maxSize）显示页数</td>
</tr>
</tbody>
</table>
